/* ========================= Main Button ======================= */

.template-gallery .event-location a,
a {
    &.button-cta,
    &.button-default,
    &.link-button {
        display: inline-block;
        padding: .7em 3%;
        color: var(--stgr);
        background: rgba(255, 255, 255, .5);
        border: 2px solid var(--wgo);
        border-radius: 4px;
        width: 100%; //        min-width: 200px;
        margin: 1em 0;
        text-align: center;
        text-decoration: none;
        text-align: center;
        &:hover,
        &:focus {
            color: var(--ssgr);
            background: var(--wgo20a); //            -webkit-box-shadow: var(--wgo60a) 0 3px 0;
            //            box-shadow: var(--wgo60a) 0 3px 0;
            text-decoration: underline;
        }
        &:visited {
            color: var(--graydk);
            border-color: var(--graymd);
        }
    }
}

//========================= Button Wells for Layout
ul {
    &.wide-button-list,
    &.ul-button-2wide {
        //        display: flex;
        padding: 0;
        margin-bottom: 0;
        list-style: none;
        @media screen and (min-width: 519px) {
            display: flex;
        }
        li {
            flex: 1;
            @media screen and (min-width: 519px) {
                flex: 1 1 50%;
                margin-right: 2%;
                &:nth-child(even) {
                    margin-right: 0;
                }
            }
            @media screen and (min-width:992px) {
                flex: 0 1 48%;
                margin-right: 4%;
                &:nth-child(even) {
                    margin-right: 0;
                }
            }
        }
    }
    &.ul-button-stack {
        padding: 0;
        list-style: none;
        margin: 1em 0;
        li {
            /*margin-bottom: 1em;*/
            .button-default {
                margin-bottom: 0;
            }
        }
    }
}


/* =================  t3/4 Showcase Button  ======================= */

.showcase {
    ul {
        list-style: none;
        padding: 0 5%;
        margin-right: 10%;
        text-align: center;
        li {
            width: 60%;
            margin: 0 auto 0 10%;
            a {
                &:link,
                &:visited {
                    font-size: 18px;
                    line-height: 1.25;
                    display: block;
                    padding: 1em 5%;
                    border: 2px solid var(--wgolt);
                    border-radius: 4px;
                    background: rgba(255, 193, 7, 1);
                    min-width: 200px;
                    text-decoration: none;
                    text-align: center;
                    color: #004e38;
                    min-width: 300px;
                    margin: 0 15% 0 10%; //                    -webkit-box-shadow: rgba(0, 0, 0, .6) 0 6px 12px;
                    //                    box-shadow: rgba(0, 0, 0, .6) 0 6px 12px;
                    @media screen and (max-width: 992px) {
                        -webkit-box-flex: 0;
                        -ms-flex: 0 0 100%;
                        flex: 0 0 100%;
                        text-align: center;
                        margin: 0 auto;
                    }
                }
                &:hover,
                &:focus {
                    border: 2px solid #fff;
                    background: rgba(255, 193, 7, .7);
                    text-decoration: none;
                    color: #fff;
                }
            }
        }
    }
}


/* =================  Exceptions ======================= */

.t5[class*="404"] {
    .ul-button-stack.three-wide {
        max-width: 100%;
        margin: 0;
        a.button-default {
            width: 100%;
        }
    }
}


